"use client";

import React, { useRef, useState, useEffect, Suspense } from 'react';
import * as THREE from 'three';
import { Canvas } from '@react-three/fiber';
import { View, OrbitControls, useGLTF, Center, Environment } from '@react-three/drei';

function Model({ url }: { url: string }) {
  // useGLTF handles the GLTFLoader and caching automatically
  const { scene } = useGLTF(url);
  
  useEffect(() => {
    if (scene) {
      // Manual scale logic to ensure the model isn't too tiny or too huge
      const box = new THREE.Box3().setFromObject(scene);
      const size = box.getSize(new THREE.Vector3());
      const maxDim = Math.max(size.x, size.y, size.z);
      const scale = 2 / maxDim; 
      scene.scale.set(scale, scale, scale);
    }
  }, [scene]);

  return <primitive object={scene} />;
}

const Scene = ({ modelUrl, panelRef }: { modelUrl: string, panelRef: React.RefObject<HTMLDivElement | null> }) => {
  return (
    <>
      <ambientLight intensity={1} />
      <pointLight position={[10, 10, 10]} intensity={1.5} />
      <Environment preset="city" />
      
      <Suspense fallback={null}>
        <Center>
          <Model url={modelUrl} />
        </Center>
      </Suspense>

      {/* Logic to ensure OrbitControls only attaches to the specific panel */}
      {panelRef.current && (
        <OrbitControls 
          makeDefault 
          enableZoom={false} 
          enablePan={false} 
          domElement={panelRef.current} 
        />
      )}
    </>
  );
};

export default function MultiScenePage() {
  const [mounted, setMounted] = useState(false);
  const containerRef = useRef<HTMLDivElement>(null!);
  const view1Ref = useRef<HTMLDivElement>(null);
  const view2Ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) return null;

  return (
    <div ref={containerRef} className="relative w-screen h-screen bg-white grid grid-cols-2 gap-4 p-8 overflow-hidden">
      
      {/* HTML DOM Panels */}
      <div ref={view1Ref} className="relative z-10 w-full h-full border-2 border-dashed border-blue-400 rounded-2xl bg-gray-50" />
      <div ref={view2Ref} className="relative z-10 w-full h-full border-2 border-dashed border-red-400 rounded-2xl bg-gray-50" />

      {/* The Global Canvas */}
      <Canvas 
        className="pointer-events-none fixed inset-0 z-20"
        camera={{ position: [0, 0, 5], fov: 45 }}
      >
        {/* We only render the View components if the Ref has captured the DOM element */}
        {view1Ref.current && (
          <View track={view1Ref as React.RefObject<HTMLElement>}>
            <Scene panelRef={view1Ref} modelUrl="https://raw.githubusercontent.com/KhronosGroup/glTF-Sample-Models/master/2.0/Duck/glTF/Duck.gltf" />
          </View>
        )}

        {view2Ref.current && (
          <View track={view2Ref as React.RefObject<HTMLElement>}>
            <Scene panelRef={view2Ref} modelUrl="https://raw.githubusercontent.com/KhronosGroup/glTF-Sample-Models/master/2.0/Box/glTF/Box.gltf" />
          </View>
        )}
      </Canvas>
    </div>
  );
}